import { QTHREEEDITOR_SELECT_OBJECT, QTHREEEDITOR_DESELECT_OBJECT, QTHREEEDITOR_OBJECT_CHANGEPOSITION, QTHREEEDITOR_OBJECT_CHANGECOLOR} from "./qthreeEditorTypes";

export const selectObject = (object) => {
    return { 
        type: QTHREEEDITOR_SELECT_OBJECT, 
        payload: object 
    }
}

export const deselectObject = () => {
    return {
        type: QTHREEEDITOR_DESELECT_OBJECT
    }
}

export const objectChangePosition = (event) => {
    return {
        type: QTHREEEDITOR_OBJECT_CHANGEPOSITION,
        payload: event
    }
}

export const objectChangeColor = (color) => {
    return {
        type: QTHREEEDITOR_OBJECT_CHANGECOLOR,
        payload: color
    }
}