import React from 'react';
import {connect} from 'react-redux';
import { Vector3 } from 'three';

const ObjectInfo = (props) => {
    const pos = props.object ? props.object.position : new Vector3(0,0,0);
    const decCnt = 5;
    return (
        <div className="topcorner whiteText">
            <span>x: {parseFloat(pos.x).toFixed(decCnt)} </span><span>y: {parseFloat(pos.y).toFixed(decCnt)} </span> <span>z: {parseFloat(pos.z).toFixed(decCnt)} </span>
        </div>
    );
}

const mapStateToProps = state => {
    return {
        object: state.qthreeEditor.object,
    }
}

export default connect(mapStateToProps)(ObjectInfo);