import * as THREE from 'three';

class PickHelper {
    constructor(handleSelectObject) {
      this.raycaster = new THREE.Raycaster();
      this.pickedObject = null;
      this.handleSelectObject = handleSelectObject;
    }
    pick(normalizedPosition, scene, camera, transformControls) {
      /*if (this.pickedObject) { // clear selected object and detach transformControls from it
        this.pickedObject = undefined;
        transformControls.detach(this.pickedObject);
      }*/
      this.raycaster.setFromCamera(normalizedPosition, camera);
      const intersectedObjects = this.raycaster.intersectObjects(scene.children);
      if (intersectedObjects.length) {
        if(intersectedObjects[0].object.canSelect) {
            transformControls.detach(this.pickedObject);
            this.pickedObject = intersectedObjects[0].object;
            this.handleSelectObject(this.pickedObject);
            transformControls.attach(this.pickedObject);
        }
      }
    }
  }

  export default PickHelper;