import React, { Component } from 'react';
import { connect } from 'react-redux';

//import {main, positionEdited} from "./ThreeCanvasScript"
import "../css/style.css";
import QThreeEditorCanvasClass from '../js/QThreeCanvasClass';
import { deselectObject, selectObject, saveFileSuccess, GLTFSceneLoaded } from '../redux';
import QThreeCameraController from './QThreeCameraController';
class QThreeEditorCanvas extends Component {
  state = {
    cameraType: 'perspective'
  }
  constructor(props){
    super(props);
    this.threeCanvas = new QThreeEditorCanvasClass(this, this.props.selectObject, this.props.deselectObject, this.handleCameraChange);
  }

  componentDidUpdate() {
    if(this.props.saveToFile){
      this.threeCanvas.saveScene(this.props.fileName);
      this.props.saveFileSuccess();
    }
    if(this.props.loadScene){
      this.threeCanvas.loadScene(this.props.sceneURL)
      this.props.GLTFSceneLoaded()
    }
  }

  componentDidMount() {
    //main(this, this.handlePositionChange);
    this.threeCanvas.mount();
  }

  handleCameraChange = (cameraType) => {
    this.setState({
      cameraType: cameraType
    })
  }

  render() 
  { 

    return ( 
        <div className="threeCanvas" ref={ref => (this.mount = ref)}>
          <QThreeCameraController 
          handleTopView={this.threeCanvas.setTopView.bind(this.threeCanvas)} 
          cameraType={this.state.cameraType} 
          changeCameraOnClick={this.threeCanvas.cameraChange.bind(this.threeCanvas)} />
        </div>
      );
  }
}

const mapStateToProps = state => {
  return {
     fileName: state.fileManager.fileName,
     saveToFile: state.fileManager.saveFile,
     sceneURL: state.fileManager.sceneFileURL,
     loadScene: state.fileManager.loadScene
  }
}

const mapDispatchToProps = dispatch => {
  return {
      selectObject: (object) => dispatch(selectObject(object)),
      deselectObject: () => dispatch(deselectObject()),
      saveFileSuccess: () => dispatch(saveFileSuccess()),
      GLTFSceneLoaded: () => dispatch(GLTFSceneLoaded())
  }
}



  export default connect(mapStateToProps,mapDispatchToProps)(QThreeEditorCanvas);

