import { FILEMANAGER_SAVE_FILE, FILEMANAGER_SAVE_SUCCESS, FILEMANAGER_FETCH_GLTF_SCENE, FILEMANAGER_GLTF_SCENE_LOADED} from './fileManagerTypes';


export const saveFile = (filename) => {
    return { 
        type: FILEMANAGER_SAVE_FILE, 
        payload: filename
    }
}

export const saveFileSuccess = () => {
    return {
        type: FILEMANAGER_SAVE_SUCCESS
    }
}

export const fetchGLTFScene = (sceneURL) => {
    return {
        type: FILEMANAGER_FETCH_GLTF_SCENE,
        payload: sceneURL
    }
} 

export const GLTFSceneLoaded = () => {
    return {
        type: FILEMANAGER_GLTF_SCENE_LOADED
    }
}