import { FILEMANAGER_SAVE_FILE, FILEMANAGER_SAVE_SUCCESS, FILEMANAGER_FETCH_GLTF_SCENE, FILEMANAGER_GLTF_SCENE_LOADED} from './fileManagerTypes';

const initialState = {
    fileName: 'scene',
    saveFile: false,
    sceneFileURL: '',
    loadScene: false
}


const fileManagerReducer = (state = initialState, action) => {
    switch (action.type) {
        case FILEMANAGER_SAVE_FILE:
            return {
                ...state,
                fileName: action.payload,
                saveFile: true
            };
        case FILEMANAGER_SAVE_SUCCESS:
            return {
                ...state,
                fileName: 'scene',
                saveFile: false
            };
        case FILEMANAGER_FETCH_GLTF_SCENE:
            return {
                ...state,
                sceneFileURL: action.payload,
                loadScene: true
            }
        case FILEMANAGER_GLTF_SCENE_LOADED:
            return {
                ...state,
                sceneFileURL: null,
                loadScene: false
            }
        
        default:
            return state;
    }
}

export default fileManagerReducer