import { QTHREEEDITOR_SELECT_OBJECT, QTHREEEDITOR_DESELECT_OBJECT, QTHREEEDITOR_OBJECT_CHANGEPOSITION, QTHREEEDITOR_OBJECT_CHANGECOLOR } from "./qthreeEditorTypes";

const initialState = {
    object: null,
}

const qthreeEditorReducer = (state = initialState, action) => {
    switch (action.type) {
        case QTHREEEDITOR_DESELECT_OBJECT:
            return {
                ...state,
                object: null,
            };
        case QTHREEEDITOR_SELECT_OBJECT:
            return {
                ...state,
                object: { ...action.payload } //need copy of object 
            };
        case QTHREEEDITOR_OBJECT_CHANGEPOSITION:
            if (state.object) {
                const pos = state.object.position;
                switch (action.payload.target.name) {
                    case "posx":
                        pos.x = parseFloat(action.payload.target.value);
                        break;
                    case "posy":
                        pos.y = parseFloat(action.payload.target.value);
                        break;
                    case "posz":
                        pos.z = parseFloat(action.payload.target.value);
                        break;
                }
                return {
                    ...state,
                    object: {
                        ...state.object,
                        position: pos
                    }
                }
            } else {
                return state
            }
        case QTHREEEDITOR_OBJECT_CHANGECOLOR:
            if(!state.object)
                return state;
            const object = {
                ...state.object,
            }
            object.material.color = action.payload;
            return {
                ...state,
                object: object
                
            }
        default: return state;
    }
}

export default qthreeEditorReducer;