import React, {useState} from 'react';
import { connect } from 'react-redux';
import { Color} from 'three';
import { objectChangeColor } from '../redux';


const ColorPicker = (props) => {
  const [r, setRed] = useState(0);
  const [g, setGreen] = useState(0);
  const [b, setBlue] = useState(0);
 
  function componentToHex (c) { 
    var hex = Number(c).toString(16);
    if (hex.length < 2) {
         hex = "0" + hex;
    }
    return hex;
  };

  function rgbToHex(r, g, b) {
    return "#" + componentToHex(r) + componentToHex(g) + componentToHex(b);
  }
  return (
    <div >
      <p>Object color</p>
      <span> Red (0-255): </span><input type="text" id="r" name="r" value={r} onChange={e => setRed(e.target.value)} /><br></br>
      <span> Green (0-255): </span><input type="text" id="g" name="g" value={g} onChange={e => setGreen(e.target.value)}  /><br></br>
      <span> Blue (0-255): </span><input type="text" id="b" name="b" value={b} onChange={e => setBlue( e.target.value)}  /><br></br>
      <span>Hex code: {rgbToHex(r, g, b)}</span><span style={{backgroundColor: rgbToHex(r, g, b) }}>{'\u00A0' + '\u00A0' + '\u00A0' +'\u00A0'+'\u00A0'}</span><br></br>
      <button onClick={() => props.objectChangeColor(new Color(r/255,g/255,b/255))}> Change color</button>
    </div>
  );
}

const mapDispatchToProps = dispatch => {
  return {
      objectChangeColor: (color) => dispatch(objectChangeColor(color)),
  }
}

export default connect(null, mapDispatchToProps)(ColorPicker);