import { GLTFExporter } from 'three/examples/jsm/exporters/GLTFExporter.js';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader';
import * as THREE from 'three';

class FileManager {
    constructor(){
        this.link = document.createElement( 'a' );
		this.link.style.display = 'none';
		document.body.appendChild(this.link )
    }
    //save file
    saveSceneToGLTF(scene, sceneName) {
        //https://threejs.org/examples/#misc_exporter_gltf
        const gltfExporter = new GLTFExporter();

        const options = {
            trs: false,
            onlyVisible: true,
            truncateDrawRange: true,
            binary: true, //glb
            maxTextureSize: Number(4096) || Infinity // To prevent NaN value
        };
        gltfExporter.parse(scene, result => {

            if (result instanceof ArrayBuffer) {

                this.saveArrayBuffer(result, sceneName + '.glb');

            } else {
                const output = JSON.stringify(result, null, 2);
                //console.log(output);
                this.saveString(output, sceneName + '.gltf');

            }

        }, options);

    }

    save( blob, filename ) {

        this.link.href = URL.createObjectURL( blob );
        this.link.download = filename;
        this.link.click();

        // URL.revokeObjectURL( url ); breaks Firefox...

    }

    saveString( text, filename ) {

        this.save( new Blob( [ text ], { type: 'text/plain' } ), filename );

    }


    saveArrayBuffer( buffer, filename ) {

        this.save( new Blob( [ buffer ], { type: 'application/octet-stream' } ), filename );

    }

    loadScene(scene, sceneURL) {
        const loader = new GLTFLoader();
        loader.load(
            // resource URL
            sceneURL,
            // called when the resource is loaded
           (gltf) =>  {
               /*const tmp = Array.from(scene.children);
               tmp.forEach( child => {
                   scene.remove(child);
               })*/
               const childrens = Array.from(gltf.scene.children);
               childrens.forEach( child => {
                   if(child instanceof THREE.Mesh) {
                       child.canSelect = true;
                       scene.add(child)
                   } else {
                       scene.add(child)
                   }

               })
                /*this.scene.children.forEach(element => {
                    this.scene.remove(element);
                });*/
                //scene.add(gltf.scene);
                /*gltf.animations; // Array<THREE.AnimationClip>
                gltf.scene; // THREE.Group
                gltf.scenes; // Array<THREE.Group>
                gltf.cameras; // Array<THREE.Camera>
                gltf.asset; // Object*/
        
            },
            // called while loading is progressing
            function ( xhr ) {
                console.log( ( xhr.loaded / xhr.total * 100 ) + '% loaded' );
            },
            // called when loading has errors
            function ( error ) {
                        console.log( 'An error happened' );
            }
        );
        
    }

}

export default FileManager;