import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { TransformControls } from 'three/examples/jsm/controls/TransformControls';
import FileManager from './FileManager';
import PickHelper from './PickHelper';

class QThreeEditorCanvasClass {
    constructor(app, handleSelectObject, handleDeselectObject, handleCameraChange) {
        const fov = 75;
        const aspect = 2;
        const near = 0.01;
        const far = 50;
        this.cameraPersp = new THREE.PerspectiveCamera(fov, aspect, near, far);
        this.cameraOrtho = new THREE.OrthographicCamera(- 600 * aspect, 600 * aspect, 600, - 600, near, far);
        this.cameraOrtho.zoom = 40;
        this.currentCamera = this.cameraPersp;
        //this.currentCamera = new THREE.PerspectiveCamera(fov, aspect, near, far);
        this.renderer = new THREE.WebGLRenderer();
        //this.renderer.setSize( window.innerWidth / 100 * width, window.innerHeight / 100 * height ); //fullscreen canvas
        this.app = app;
        this.currentCamera.position.z = 2;
        this.scene = new THREE.Scene();
        const color = 0xFFFFFF;
        const intensity = 1;
        this.light = new THREE.DirectionalLight(color, intensity);
        this.light.position.set(-1, 2, 4);
        this.light2 = new THREE.AmbientLight(0x404040);
        const boxWidth = 1;
        const boxHeight = 1;
        const boxDepth = 1;
        this.cubeGeometry = new THREE.BoxGeometry(boxWidth, boxHeight, boxDepth);
        this.orbitControl = new OrbitControls(this.currentCamera, this.renderer.domElement);
        const size = 10;
        const divisions = 10;
        this.gridHelper = new THREE.GridHelper(size, divisions);
        this.gridHelper.canSelect = false;
        this.transformControls = new TransformControls(this.currentCamera, this.renderer.domElement);
        this.selectedObject = null;
        this.transformControls.addEventListener('objectChange', this.updateSelected);
        this.transformControls.addEventListener('dragging-changed', this.transformDragging);
        this.handleSelectObject = handleSelectObject;
        this.handleDeselectObject = handleDeselectObject;
        this.handleCameraChange = handleCameraChange;
        this.pickHelper = new PickHelper(handleSelectObject);
        this.pickPosition = { x: -10000, y: -10000 };
    }

    transformDragging = (event) => {

        this.orbitControl.enabled = !event.value;

    }

    updateSelected = () => {
        if (this.pickHelper.pickedObject) {
            this.handleSelectObject(this.pickHelper.pickedObject);
        } else {
            this.handleDeselectObject();
        }
    }
    //css styles
    mount(saveToFile) {
        this.saveToFile = saveToFile;
        this.renderer.domElement.id = 'QThreeEditorCanvas';
        this.app.mount.appendChild(this.renderer.domElement);
        this.scene.add(this.light);
        this.scene.add(this.light);
        this.cubes = [
            this.makeInstance(this.cubeGeometry, 0x44aa88, 0, this.scene),
            this.makeInstance(this.cubeGeometry, 0x8844aa, -2, this.scene),
            this.makeInstance(this.cubeGeometry, 0xaa8844, 2, this.scene),
        ];
        this.scene.add(this.gridHelper);
        this.scene.add(this.transformControls);

        window.addEventListener('keydown', (event) => {
            switch (event.key) {
                case "g":
                    this.transformControls.setMode("translate")
                    break
                case "r":
                    this.transformControls.setMode("rotate")
                    break
                case "s":
                    this.transformControls.setMode("scale")
                    break
                case "c":
                    this.cameraChange();
                    break;
                case "u":
                    this.saveScene('scene');
                    break;

            }
        })
        this.renderer.domElement.addEventListener('pointerdown', this.onclick.bind(this), true);
        //start render scene
        this.renderer.render(this.scene, this.currentCamera);
        requestAnimationFrame(() => { this.render(); });
    }

    saveScene(fileName){
        const fileMng = new FileManager();
        fileMng.saveSceneToGLTF(this.scene, fileName);
    }

    loadScene(sceneURL){
        const fileMng = new FileManager();
        fileMng.loadScene(this.scene, sceneURL);
    }
    

    setTopView() {
        this.currentCamera.position.x = 0;
        this.currentCamera.position.y = 8;
        this.currentCamera.position.z = 0;
        this.currentCamera.lookAt(this.scene.position);
    }

    cameraChange() {
        const position = this.currentCamera.position.clone();

        this.currentCamera = this.currentCamera.isPerspectiveCamera ? this.cameraOrtho : this.cameraPersp;
        this.currentCamera.position.copy(position);

        this.orbitControl.object = this.currentCamera;
        this.transformControls.camera = this.currentCamera;

        this.currentCamera.lookAt(this.orbitControl.target.x, this.orbitControl.target.y, this.orbitControl.target.z);
        if (this.currentCamera == this.cameraOrtho){
            this.handleCameraChange('ortho');
        } else {
            this.handleCameraChange('perspective');
        }
        const canvas = this.renderer.domElement;
        const aspect = canvas.clientWidth / canvas.clientHeight;
        const pixelRatio = window.devicePixelRatio;
        this.cameraPersp.aspect = aspect;
        this.cameraPersp.updateProjectionMatrix();

        this.cameraOrtho.left = this.cameraOrtho.bottom * aspect;
        this.cameraOrtho.right = this.cameraOrtho.top * aspect;
        this.cameraOrtho.updateProjectionMatrix();
        const width = canvas.clientWidth * pixelRatio | 0;
        const height = canvas.clientHeight * pixelRatio | 0;

        this.renderer.setSize(width, height, false);

        this.render();

    }


    resizeRendererToDisplaySize(renderer) {
        const canvas = this.renderer.domElement;
        const pixelRatio = window.devicePixelRatio;
        const width = canvas.clientWidth * pixelRatio | 0;
        const height = canvas.clientHeight * pixelRatio | 0;
        const needResize = canvas.width !== width || canvas.height !== height;
        if (needResize) {
            this.renderer.setSize(width, height, false);
        }
        return needResize;
    }

    render() {
        const canvas = this.renderer.domElement;
        if (this.resizeRendererToDisplaySize(this.renderer)) {
            this.currentCamera.aspect = canvas.clientWidth / canvas.clientHeight;
            this.currentCamera.updateProjectionMatrix();
        }
        this.currentCamera.aspect = canvas.clientWidth / canvas.clientHeight;
        this.currentCamera.updateProjectionMatrix();
        this.renderer.render(this.scene, this.currentCamera);
        if(this.saveToFile){
            saveScene('test');
        }

        requestAnimationFrame(() => { this.render(); });
    }
    onclick(event) {
        this.setPickPosition(event);
        this.pickHelper.pick(this.pickPosition, this.scene, this.currentCamera, this.transformControls);
    }

    getCanvasRelativePosition(event) {
        const canvas = this.renderer.domElement;
        const rect = canvas.getBoundingClientRect();
        return {
            x: (event.clientX - rect.left) * canvas.width / rect.width,
            y: (event.clientY - rect.top) * canvas.height / rect.height,
        };
    }

    setPickPosition(event) {
        const canvas = this.renderer.domElement;
        const pos = this.getCanvasRelativePosition(event);
        this.pickPosition.x = (pos.x / canvas.width) * 2 - 1;
        this.pickPosition.y = (pos.y / canvas.height) * -2 + 1;  // note we flip Y
    }

    makeInstance(geometry, color, x, scene) {
        const material = new THREE.MeshPhongMaterial({ color });

        const cube = new THREE.Mesh(geometry, material);
        cube.canSelect = true;
        this.scene.add(cube);

        cube.position.x = x;

        return cube;
    }
}

export default QThreeEditorCanvasClass;