import React from 'react';
import "../css/style.css";

function QThreeCameraController({handleTopView, cameraType, changeCameraOnClick}) {
    return (
        <div className="topLeftCorner">
            <button onClick={() => handleTopView()}> Top view</button> <span className="whiteText"> Camera info: {cameraType}</span> <br></br>
            <button onClick={changeCameraOnClick} > Change camera to {cameraType === 'perspective' ? 'ortographic' : 'perspective' } </button>
        </div>
    );
}

export default QThreeCameraController;