import React, {useState, Component} from 'react';
import { connect } from 'react-redux';
import { saveFile, fetchGLTFScene } from '../redux';

class SaveFileForm extends Component {
    constructor(props){
        super(props);
        this.fileInput = React.createRef();
    }
    state = {
        fileName: this.props.fileName
    }

    handleFileName(event){
        this.setState({
            ...state,
            fileName: e.target.value
        })
    }
    checkExtension(fileName){
        const extension = fileName.slice((fileName.lastIndexOf(".") - 1 >>> 0) + 2);
        if(extension === 'glb'){
            return true;
        } else {
            return false;
        }
    }
    handleUploadFile(event) {
        event.preventDefault();
        const file = this.fileInput.current.files[0];
        if(!file || !this.checkExtension(file.name)){
            alert('This is not gltf scene file.');
        } else {
            const URL = window.URL.createObjectURL(file);
            this.props.uploadSceneFile(URL);
        }
      }

    render(){
        return (
            <div>
                <span>Filename: </span><input type="text" value={this.state.fileName} onChange={(e) => this.handleFileName}></input>
                <button onClick={() => this.props.saveFile(this.state.fileName)}>Export scene to GLB file</button> <br></br>
                <input type='file' ref={this.fileInput}></input>
                <button onClick={(e) => this.handleUploadFile(e)}>Upload file</button>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
       fileName: state.fileManager.fileName,
    }
 }

const mapDispatchToProps = dispatch => {
    return {
        saveFile: (fileName) => dispatch(saveFile(fileName)),
        uploadSceneFile: (sceneURL) => dispatch(fetchGLTFScene(sceneURL))
    }
  }

export default connect(mapStateToProps, mapDispatchToProps)(SaveFileForm);