import React from 'react';
import { connect } from 'react-redux';
import { Vector3 } from 'three';
import { objectChangePosition } from '../redux';

const PositionInputField = (props) => {
   const position = props.object ? props.object.position : new Vector3(0,0,0);
   return (
      <div>
         <span className="whiteText">x: </span><input type="text" id="posx" name="posx" value={position.x} onChange={props.objectChangePosition} /><br></br>
         <span className="whiteText">y: </span><input type="text" id="posy" name="posy" value={position.y} onChange={props.objectChangePosition} /><br></br>
         <span className="whiteText">z: </span><input type="text" id="posz" name="posz" value={position.z} onChange={props.objectChangePosition} /><br></br>
      </div>
   );
}

const mapStateToProps = state => {
   return {
      object: state.qthreeEditor.object,
   }
}
const mapDispatchToProps = dispatch => {
   return {
       objectChangePosition: (event) => dispatch(objectChangePosition(event)),
   }
 }
export default connect(mapStateToProps, mapDispatchToProps )(PositionInputField)